<template>
    <DashboardContainer>
        <CountersGrid v-if="$storage.get('auth').user.user_type != 2" />
        <GraphGrid1 v-if="$storage.get('auth').user.user_type != 2"/>
        <GraphGrid2 v-if="$storage.get('auth').user.user_type != 2"/>
        <div class="saleperson-mid" v-if="$storage.get('auth').user.user_type == 2">
            <CountersGrid/>
            <GraphGrid1/>
            <GraphGrid2/>
        </div>
        <div class="saleperson-right" v-if="$storage.get('auth').user.user_type == 2">
            <Appointments :employeesList="allEmployees" :defaultAssignTo="assignTo" />
            <tasks :employeesList="allEmployees" :defaultAssignTo="assignTo"/>
            <Notes/>
        </div>
    </DashboardContainer>
</template>

<script>
import DashboardContainer from '../containers/DashboardContainer'
import CountersGrid from '../components/dashboard/CountersGrid'
import GraphGrid1 from '../components/dashboard/GraphGrid1'
import GraphGrid2 from '../components/dashboard/GraphGrid2'

import Appointments from '../components/leads/Appointments'
import Tasks from '../components/leads/Tasks'
import Notes from '../components/leads/Notes'

export default {
    name: 'Dashboard',
    data(){
		return{
            allEmployees:[],
            assignTo:'',
            //LDetail: {}
        }
    },
    beforeCreate: function () {
        var auth = this.$storage.get('auth')
        if (!auth) {
            this.$router.push('/login')
        }else{
            if(!auth.token){
                this.$router.push('/login')
            }
        }
    },
    components: {
        DashboardContainer,
        CountersGrid,
        GraphGrid1,
        GraphGrid2,
        Appointments,
        Tasks,
        Notes
    }
}
</script>